body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu,
    Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:
  /* top, transparent black, faked with gradient */ linear-gradient(
      rgba(40, 36, 0, 0.5),
      rgba(0, 0, 40, 0.5)
    ),
    /* bottom, image */
      url('https://cdn.pixabay.com/photo/2020/07/11/09/17/torino-5393338_960_720.jpg');
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

* {
  font-family: 'Verdana', cursive, sans-serif;
  color: #ffffff;
}

h1,
h2 {
  text-align: center;
  color: rgb(245, 245, 245);
  text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.75);
}

.app {
  width: 300px;
  min-height: 300px;
  height: 50vh;
  background-image: linear-gradient(#252d4a, rgba(207, 193, 0, 0.329), rgba(40, 36, 0));
  border-radius: 15px;
  padding: 10px;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.75);
  display: grid;
  justify-content: space-evenly;
  text-align: center;
  position: relative;
}

.score-section {
  display: flex;
  font-size: 24px;
  align-items: center;
  justify-content: center;
}

/* QUESTION/TIMER/LEFT SECTION */
.question-section {
  width: 100%;
  height: 5rem;
  position: relative;
}

.question-count {
  margin-bottom: 20px;
}

.question-count span {
  font-size: 28px;
}

.question-text {
  line-height: 1.5rem;
}

/* ANSWERS/RIGHT SECTION */
.answer-section {
  width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.button-answer {
  width: 100%;
  height: 3rem;
  font-size: 16px;
  color: #ffffff;
  background-color: #252d4a;
  border-radius: 15px;
  display: flex;
  margin-bottom: 10px;
  padding: 5px 10px;
  justify-content: flex-start;
  align-items: center;
  border: 3px solid rgb(87, 81, 1);
  cursor: pointer;
}

.button-answer:focus {
  outline: none;
}

.button-answer svg {
  margin-right: 5px;
}

.button-reset {
  width: 60%;
  height: 3rem;
  font-size: 16px;
  color: #ffffff;
  background-color: #252d4a;
  border-radius: 15px;
  display: flex;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
  padding: 5px;
  justify-content: center;
  align-items: center;
  border: 5px solid rgb(87, 81, 1);
  cursor: pointer;
}

.button-reset:focus {
  outline: none;
}

.image {
  width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
img {
  transform: scale(0.5);
  border-radius: 15px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
}

footer {
  margin-top: 1rem;
  font-size: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.75);
}

@media screen and (min-width: 1025px) {
  button {
    transition: all 0.2s ease-in-out;
  }
  button:hover {
    background-color: #555e7d;
    transition: all 0.2s ease-in-out;
  }
}
